import React, { useEffect, useState } from "react";
import Modal from "react-modal";
import { GrClose } from "react-icons/gr";
import { HiCurrencyRupee } from "react-icons/hi";
import RazorpayPaymentScreen from "../../../Components/RazorpayPaymentScreen";
import { HEADER } from "../../Trade/tradeComponent/TradeApiListFile";
import axios from "axios";
import WaterApiList from '../../../Components/ApiList/WaterApiList';
// import { Document, Page, pdfjs } from 'react-pdf';

const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',
        backgroundColor: 'transparent',
        border: 'none',
        zIndex: "1000",
    },
    overlay: { zIndex: 1000 }

};

const customStyles1 = {
    content: {
        position: "fixed",
        display: "flex",
        justifyContent: "center",
        top: "5%",
        left: "25%",
        width: "50%",
        height: "95%",
        background: "#9be7fa",
        // zindex: "1000",
        overflowY: "auto"
    },
    overlay: { zIndex: 1000 }
};


Modal.setAppElement('#root');

function ViewUploadedDoc(props) {
    const [numPages, setNumPages] = useState(null);
    const [pageNumber, setPageNumber] = useState(1);
    // Modal.setAppElement('#yourAppElement');
    const [modalIsOpen, setIsOpen] = React.useState(false);
    const [transData, setTransData] = useState()

    const { api_rzpWaterOrderId, api_getTranNo } = WaterApiList();

    useEffect(() => {
        if (props.openDocviewModal > 0) setIsOpen(true);
    }, [props.openDocviewModal]);

    function afterOpenModal() { }

    function closeModal() {
        setIsOpen(false);
    }

    console.log("DOCUMENT URL :", props?.documentUrl)

    return (
        <div className="">
            <Modal
                isOpen={modalIsOpen}
                onAfterOpen={afterOpenModal}
                onRequestClose={closeModal}
                style={customStyles}
                contentLabel="Example Modal"
            >
                <div style={{ 'width': '71.5vw', 'height': '90vh' }}>
                    <div className="bg-indigo-200 shadow-2xl border border-sky-200 p-3 rounded-md z-50" >
                        <div onClick={closeModal} className="absolute cursor-pointer bg-green-100 hover:bg-gray-300 p-1 rounded-md"><GrClose /> </div>
                        <div className='py-2 pl-3 font-semibold text-center text-2xl text-gray-800'> Uploaded Document</div>
                        <div className="scroll-auto">

                            {props?.documentUrl?.split('.').pop() == "pdf" ?
                                <iframe style={{ 'width': '70vw', 'height': '50vh' }} src={props?.documentUrl} frameborder="0"></iframe>
                                : <div className="flex justify-center">
                                    < img src={props?.documentUrl} className="rounded-md" alt="Uploaded Image" srcset="" />
                                </div>
                            }
                        </div>
                        {/* {props?.documentUrl} */}
                        <div className='flex justify-center mt-5'>
                            <button onClick={closeModal} className='mx-2 bg-indigo-600 hover:bg-indigo-700 transition duration-200 hover:scale-105 font-normal text-white px-6 py-1 text-sm rounded-sm shadow-xl'>Close</button>
                        </div>
                    </div>


                </div>
            </Modal>
        </div>
    );
}

// ReactDOM.render(<App />, appElement);

export default ViewUploadedDoc;
